import {Canvas, useFrame} from "@react-three/fiber";
import React, {FC, useRef} from "react";
import * as THREE from "three";
import {OrbitControls, PerspectiveCamera} from "@react-three/drei";
import {random, randomInteger} from "../../helpers/helpers";

const COUNT = 180;
const MAX_RADIUS = 30;
const MIN_DISTANCE_Z = -80;
const MAX_DISTANCE_Z = 60;
const SPEED = 5;
const SPEED_RADIAL = 0.25;

export const Ball: FC<JSX.IntrinsicElements['mesh']> = (props) => {
    const ref = useRef<THREE.Mesh>(null!)

    useFrame((state, delta) => {
        state.camera.lookAt(0, 0, 0);

        const radius = Math.sqrt((ref.current.position.x ** 2) + (ref.current.position.y ** 2))
        let alfa = Math.atan(ref.current.position.y / ref.current.position.x)
        if (ref.current.position.x < 0) {
            alfa += Math.PI;
        }

        ref.current.position.x = radius * Math.cos(alfa + SPEED_RADIAL * delta);
        ref.current.position.y = radius * Math.sin(alfa + SPEED_RADIAL * delta);

        if (ref.current.position.z > MAX_DISTANCE_Z) {
            ref.current.position.z = MIN_DISTANCE_Z;
        } else {
            ref.current.position.z += SPEED * delta;
        }
    })

    return (
        <mesh {...props}
              ref={ref}
        >
            <sphereGeometry args={[1, 12, 12]}/>
            <meshPhongMaterial
                color={`rgb(${randomInteger(0, 255)},${randomInteger(0, 255)},${randomInteger(0, 255)})`}
                shininess={100}
            />
        </mesh>
    )
}

export const Spiral = () => {

    const array = [];
    for (let i = 0; i < COUNT; i++) {
        array.push(i);
    }

    return (
        <Canvas>
            <color attach="background" args={["black"]}/>
            <ambientLight intensity={0.5}/>
            {/*<spotLight position={[10, 10, 10]} angle={0.15} penumbra={1}/>*/}
            <directionalLight position={[-30, 30, 30]} intensity={2}/>

            {
                array.map((el) => (
                    <Ball key={el}
                          position={[
                              random(0, MAX_RADIUS) * Math.cos(random(0, 2 * Math.PI)),
                              random(0, MAX_RADIUS) * Math.sin(random(0, 2 * Math.PI)),
                              random(MIN_DISTANCE_Z, MAX_DISTANCE_Z)
                          ]}
                    />
                ))
            }

            <PerspectiveCamera makeDefault
                               position={[45, 30, 15]}
            />
            <OrbitControls target={[0, 0, 0]} enableRotate={true} enableZoom={true}/>
            <fogExp2 attach="fog"
                     color="#000"
                     density={0.005}
            />
        </Canvas>
    )
}