import {Canvas, useFrame} from "@react-three/fiber"
import React, {FC, useRef, useState} from "react";
import * as THREE from "three";
import {OrbitControls, PerspectiveCamera, useTexture} from "@react-three/drei";
import boxImg from "../../assets/jpeg/box.jpg";
import boxImg1 from "../../assets/jpeg/box 1.jpg";
import boxImg2 from "../../assets/jpeg/box 2.jpg";
import boxImg3 from "../../assets/jpeg/box 3.jpg";
import boxImg4 from "../../assets/jpeg/box 4.jpg";
import boxImg5 from "../../assets/jpeg/box 5.jpg";
import boxImg6 from "../../assets/jpeg/box 6.jpg";
import {RepeatWrapping, Vector3} from "three";

//================== BOX1 =============================
const Box1: FC<JSX.IntrinsicElements['mesh']> = (props) => {
    const ref = useRef<THREE.Mesh>(null!)
    useFrame((state, delta) => {
        ref.current.rotation.x += -0.01
        ref.current.rotation.z += 0.01
    })

    const [map] = useTexture([boxImg])
    map.repeat = new THREE.Vector2(1,1)
    map.wrapS = RepeatWrapping;
    map.wrapT = RepeatWrapping;

    return (
        <mesh ref={ref} {...props}>
            <boxGeometry args={[1, 1, 1]} />
            <meshStandardMaterial map={map}/>
        </mesh>
    )
}

//================== BOX2 =============================
const Box2: FC<JSX.IntrinsicElements['mesh']> = (props) => {
    const ref = useRef<THREE.Mesh>(null!)
    useFrame((state, delta) => {
        ref.current.rotation.x += -0.01
        ref.current.rotation.z += 0.01
    })

    const [map] = useTexture([boxImg])
    const mapClone1 = map.clone();
    mapClone1.repeat = new THREE.Vector2(2,2)
    mapClone1.wrapS = THREE.RepeatWrapping;
    mapClone1.wrapT = THREE.RepeatWrapping;

    return (
        <mesh ref={ref} {...props}>
            <boxGeometry args={[1, 1, 1]} />
            <meshStandardMaterial map={mapClone1}/>
        </mesh>
    )
}

//================== BOX3 =============================
const Box3: FC<JSX.IntrinsicElements['mesh']> = (props) => {
    const ref = useRef<THREE.Mesh>(null!)
    useFrame((state, delta) => {
        ref.current.rotation.x += -0.01
        ref.current.rotation.z += 0.01
    })

    const [map] = useTexture([boxImg])
    const mapClone2 = map.clone()
    mapClone2.repeat = new THREE.Vector2(2,2)
    mapClone2.wrapS = THREE.ClampToEdgeWrapping;
    mapClone2.wrapT = THREE.ClampToEdgeWrapping;

    return (
        <mesh ref={ref} {...props}>
            <boxGeometry args={[1, 1, 1]}/>
            <meshStandardMaterial map={mapClone2}/>
        </mesh>
    )
}

//================== BOX4 =============================
const Box4: FC<JSX.IntrinsicElements['mesh']> = (props) => {
    const ref = useRef<THREE.Mesh>(null!)
    useFrame((state, delta) => {
        ref.current.rotation.x += -0.01
        ref.current.rotation.z += 0.01
    })

    const [map] = useTexture([boxImg])
    const mapClone4 = map.clone();
    mapClone4.offset.set(0.25, 0.25);

    return (
        <mesh ref={ref} {...props}>
            <boxGeometry args={[1, 1, 1]}/>
            <meshStandardMaterial map={mapClone4}/>
        </mesh>
    )
}

//================== BOX5 =============================
const Box5: FC<JSX.IntrinsicElements['mesh']> = (props) => {
    const ref = useRef<THREE.Mesh>(null!)
    useFrame((state, delta) => {
        ref.current.rotation.x += -0.01
        ref.current.rotation.z += 0.01
    })

    const [map] = useTexture([boxImg])
    const mapClone5 = map.clone();
    mapClone5.center.set(0.5,0.5);
    mapClone5.rotation = THREE.MathUtils.degToRad(45);

    return (
        <mesh ref={ref} {...props}>
            <boxGeometry args={[1, 1, 1]}/>
            <meshStandardMaterial map={mapClone5}/>
        </mesh>
    )
}

//================== BOX10 =============================
const Box10: FC<JSX.IntrinsicElements['mesh']> = (props) => {
    const ref = useRef<THREE.Mesh>(null!)
    useFrame((state, delta) => {
        ref.current.rotateOnAxis(new Vector3(1,1.0, 0).normalize(), 0.02)
    })
    const materials = useTexture([boxImg1, boxImg2, boxImg3, boxImg4, boxImg5, boxImg6])
    const [hover, setHover] = useState(false)

    return (
        <mesh ref={ref} {...props}
              onPointerOver={event => setHover(true)}
              onPointerOut={event => setHover(false)}
              //rotation={hover ? new THREE.Euler(-0.5 * Math.PI,-0.5 * Math.PI,-0.5 * Math.PI) : new THREE.Euler(0,0,0)}
        >
            <boxGeometry args={[1, 1, 1]}/>
            {
                materials.map((map, index) => (
                        <meshBasicMaterial key={index}
                                           attach={`material-${index}`}
                                           map={map}
                        />
                    )
                )
            }
        </mesh>
    )
}

//=====================================================
//================== TEXTURE ==========================
//=====================================================
export const Texture = () => {
    return (
        <Canvas>
            <ambientLight intensity={0.5}/>
            <spotLight position={[10, 10, 10]} angle={0.15} penumbra={1}/>
            <Box1 position={[-4, 1, 0]}/>
            <Box2 position={[-2, 1, 0]}/>
            <Box3 position={[0, 1, 0]}/>
            <Box4 position={[2, 1, 0]}/>
            <Box5 position={[4, 1, 0]}/>
            <Box10 position={[-4, -1, 0]}/>
            <PerspectiveCamera makeDefault
                               position={[0,0,6]}
            />
            <OrbitControls target={[0,0,0]}/>
        </Canvas>
    )
}