import React, {FC, useRef} from "react";
import style from "./RotateByScroll.module.scss"
import {Canvas, useFrame} from "@react-three/fiber";
import {lorem1000} from "./const";
import {observer} from "mobx-react-lite";
import {useStore} from "../../store/useStore";
import {Box, OrbitControls, PerspectiveCamera} from "@react-three/drei";
import * as THREE from "three";

interface IBoxComponent {
    autorotate: boolean
    pageYOffset: number
}
const BoxComponent: FC<IBoxComponent> = observer(({autorotate, pageYOffset}) => {
    const ref = useRef<THREE.Mesh>(null!)
    console.log(pageYOffset)
    useFrame((state, delta) => {
            if (autorotate) {
                ref.current.rotation.y += delta
            } else {
                ref.current.rotation.y = 0.001 * pageYOffset;
            }
        }
    )
    return (
        <Box args={[1, 1, 1]} ref={ref}>
            <meshPhongMaterial color="green"/>
        </Box>
    )
})

interface IRotatingBox { autorotate?: boolean }
const RotatingBox: FC<IRotatingBox> = observer(({autorotate = true}) => {
    const {appStore: {pageYOffset}} = useStore();
    return (
        <Canvas className={style.box}>
            <ambientLight intensity={0.5}/>
            <directionalLight position={[-1, 2, 4]} intensity={2}/>
            <BoxComponent autorotate={autorotate} pageYOffset={pageYOffset}/>
            <PerspectiveCamera makeDefault position={[3, 2.4, 3]} zoom={2.5} />
            <OrbitControls enableRotate={false} enableZoom={false}/>
        </Canvas>
    )
})


export const RotateByScroll = () => {
    return (
        <div className={style.rotateByScroll}>
            <div className={style.inner}>
                <div className={style.texts}>
                    <p>{lorem1000}</p>
                    <p>{lorem1000}</p>
                    <p>{lorem1000}</p>
                    <p>{lorem1000}</p>
                </div>

                <div className={style.boxWrapper1}>
                    <RotatingBox/>
                </div>

                <div className={style.boxWrapper2}>
                    <RotatingBox/>
                </div>

                <div className={style.boxWrapper3}>
                    <RotatingBox autorotate={false}/>
                </div>

                <div className={style.boxWrapper4}>
                    <RotatingBox autorotate={false}/>
                </div>

            </div>
        </div>
    )
}