import {Canvas, useFrame} from "@react-three/fiber";
import React, {useRef} from "react";
import {OrbitControls, PerspectiveCamera} from "@react-three/drei";
import {BufferAttribute, Color, ShaderLib} from "three";
import * as THREE from "three";
import {getIndexEnd, getIndexStart} from "../../helpers/helpers";
import {pointShaderMaterial} from "./pointShaderMaterial";

const CIRCLE_COUNT = 60;
const DELTA_R = 10;
const DELTA_L = 10;
const AMP = 4;
const OMEGA_T = 3;
const OMEGA_R = 0.5;

const circleLength = [] as number[]

const Points = () => {
    const positions = [] as number[];
    const scales = [] as number[];
    const colors = [] as number[]

    for (let i = 0; i < CIRCLE_COUNT; i++) {
        let N_i = 0;
        if (i === 0) {
            positions.push(...[0, 0, 0])
            N_i = 1;
            scales.push(10);
            colors.push(...[255, 0, 0])
        } else {
            const length_i = 2 * Math.PI * i * DELTA_R;
            N_i = Math.floor(length_i / DELTA_L);

            const radius_i = DELTA_R * i;

            for (let j = 0; j < N_i; j++) {
                const x_j = radius_i * Math.cos(2 * Math.PI * j / N_i);
                const y_j = radius_i * Math.sin(2 * Math.PI * j / N_i);
                const z_j = 0;
                positions.push(...[x_j, y_j, z_j])
                scales.push(10);
                colors.push(...[255, 0, 0])
            }
        }
        circleLength[i] = N_i;
    }

    const positionAttribute = new BufferAttribute(new Float32Array(positions), 3);
    const scaleAttribute = new BufferAttribute(new Float32Array(scales), 1);
    const colorAttribute = new BufferAttribute(new Float32Array(colors), 3);

    useFrame((state, delta) => {
        const scales = Array.from(ref.current.geometry.getAttribute("scale").array);
        const positions = Array.from(ref.current.geometry.getAttribute("position").array);
        //const colors = Array.from(ref.current.geometry.getAttribute("color").array);

        const time = state.clock.elapsedTime

        for (let index = 0; index < circleLength.length; index++ ) {
            const indexStart = getIndexStart(circleLength, index);
            const indexEnd = getIndexEnd(circleLength, index);
            for (let j = indexStart; j < indexEnd; j++) {
                if ((j + 1) % 3 === 0) {
                    positions[j] = AMP * Math.sin(1 * OMEGA_T * time + 1 * OMEGA_R * index );
                }
            }
        }

        for (let i = 0; i < scales.length; i++) {
            scales[i] = 5 + 5 * (positions[(i +1 ) * 3 - 1] + AMP) / (2 * AMP);
        }

        const scaleAttributeUpdate = new BufferAttribute(new Float32Array(scales), 1);
        const positionAttributeUpdate = new BufferAttribute(new Float32Array(positions), 3);
        ref.current.geometry.setAttribute("scale", scaleAttributeUpdate);
        ref.current.geometry.setAttribute("position", positionAttributeUpdate);
    })

    const ref = useRef<THREE.Points>(null!)

    return (
        <points ref={ref}>
            <bufferGeometry>
                <bufferAttribute attach="attributes-position" {...positionAttribute} needsUpdate={true}/>
                <bufferAttribute attach="attributes-scale" {...scaleAttribute} needsUpdate={true}/>
                <bufferAttribute attach="attributes-customColor" {...colorAttribute} needsUpdate={true}/>
            </bufferGeometry>
            <shaderMaterial attach="material" {...pointShaderMaterial}/>
        </points>
    )
}

export const BufferPoints = () => {
    return (
        <Canvas gl={{antialias: true}}>
            <color attach="background" args={["grey"]}/>
            <PerspectiveCamera makeDefault position={[0, -160, 80]}/>
            <OrbitControls target={[0, 0, 0]} enableRotate={true} enableZoom={true}/>
            <Points/>
        </Canvas>
    )
}