import React, {FC, useEffect, useRef, useState} from "react";
import {Canvas, useFrame} from "@react-three/fiber";
import {OrbitControls, PerspectiveCamera} from "@react-three/drei";
import * as THREE from "three";
import {Vector3} from "three";

const COUNT = 2
const DELTA = 0.5;
const half = (COUNT + (COUNT - 1) * DELTA) * 0.5;


export const Box: FC<JSX.IntrinsicElements['mesh'] &
    { movementX: number, movementY: number }> = ({
                                                     movementX,
                                                     movementY,
                                                     ...props
                                                 }) => {
    const [inside, setInside] = useState(false)
    const [move, setMove] = useState(false)
    const [movementXLocal, setMovementXLocal] = useState(0);
    const [movementYLocal, setMovementYLocal] = useState(0);

    useEffect(() => {
        if (inside) {
            setMovementXLocal(movementX)
        }
    }, [movementXLocal])
    useEffect(() => {
        if (inside) {
            setMovementYLocal(movementY)
        }
    }, [movementYLocal])

    const ref = useRef<THREE.Mesh>(null!)

    useFrame((state, delta) => {
        console.log(move)
        //state.camera.lookAt(half, half, 0);
        //if (move) {
            ref.current.rotateOnAxis(new Vector3(
                1, //movementYLocal,
                1, //movementXLocal
                0
            ).normalize(), 0.02)
        //}

    })



    const onPointerEnterHandler = () => {
        console.log("onPointerEnterHandler")
        setMove(true)
        setInside(true)
    }

    const onPointerLeaveHandler = () => {
        console.log("onPointerLeaveHandler")
        //setInside(false)
    }

    return (
        <mesh {...props}
              ref={ref}
              onPointerEnter={onPointerEnterHandler}
              onPointerLeave={onPointerLeaveHandler}
        >
            <boxGeometry args={[1, 1, 1]}/>
            <meshBasicMaterial
                color="white"
                wireframe={true}
            />
        </mesh>
    )
}


export const RotateByMouse = () => {

    const arr = [] as number[]
    for (let i = 0; i < COUNT; i++) {
        arr.push(i)
    }

    const [movementX, setMovementX] = useState(0);
    const [movementY, setMovementY] = useState(0);

    const onMouseMoveHandler = (e: any) => {
        setMovementX(e.movementX)
        setMovementY(e.movementY)
    }

    return (
        <Canvas
            //onMouseMove={(e) => onMouseMoveHandler(e)}
        >
            <color attach="background" args={["black"]}/>
            <ambientLight intensity={0.5}/>
            <spotLight position={[10, 10, 10]} angle={0.15} penumbra={1}/>
            <directionalLight position={[-30, 30, 30]} intensity={2}/>


            {
                arr.map(i => (
                    <group key={i}>
                        {
                            arr.map(j => (
                                <Box key={j} position={[i + i * DELTA, j + j * DELTA, 0]}
                                     movementX={movementX}
                                     movementY={movementY}
                                />
                            ))
                        }
                    </group>

                ))
            }


            <PerspectiveCamera makeDefault
                               position={[half, half - 5, 15]}
            />
            <OrbitControls target={[half, half, 0]} enableRotate={true} enableZoom={true}/>
            <fogExp2 attach="fog"
                     color="red"
                     //near={1}
                     //far={10}
                density={0.0025}
            />
        </Canvas>
    )
}