import {Canvas, useFrame} from "@react-three/fiber";
import React, {FC, useRef, useState} from "react";
import {Cylinder, OrbitControls, PerspectiveCamera, Sphere, Torus} from "@react-three/drei";
import {DoubleSide, Euler, Plane, Vector3} from "three";

//===================== MATERIAL ===================//
const Material: FC<JSX.IntrinsicElements['meshStandardMaterial']> = (props) => {
    return (
        <meshStandardMaterial
            color="#CCC"
            emissive="#444"
            roughness={0.4}
            metalness={1}
            {...props}
        />
    )
}

//===================== AXIS ===================//
const Axis: FC<{ position: number, radius?: number, length?: number }> = ({position, radius = 0.5, length = 4}) => {
    return (
        <>
            <Cylinder name="axis left"
                      args={[radius, radius, length, 12, 1]}
                      rotation={new Euler(0, 0, 0.5 * Math.PI)}
                      position={[-position, 0, 0]}
            >
                <Material/>
            </Cylinder>
            <Cylinder name="axis right"
                      args={[radius, radius, length, 12, 1]}
                      rotation={new Euler(0, 0, 0.5 * Math.PI)}
                      position={[position, 0, 0]}
            >
                <Material/>
            </Cylinder>
        </>

    )
}

//===================== CIRCLE 1 ===================//
const Circle1 = () => {
    const ref = useRef<THREE.Mesh>(null!)
    const [forward, setForward] = useState(true);
    useFrame((state, delta) => {
        if (forward) {
            ref.current.rotation.x += delta
            if (ref.current.rotation.x >= 0.5 * Math.PI) {
                setForward(false)
            }
        } else {
            ref.current.rotation.x -= delta
            if (ref.current.rotation.x <= -0.5 * Math.PI) {
                setForward(true)
            }
        }
    })
    return (
        <Torus ref={ref} args={[30, 1, 16, 50]}>
            <Material/>
            <Circle2/>
        </Torus>
    )
}

