//===================== CIRCLE 4 ===================//
const Circle4 = () => {
    const ref = useRef<THREE.Group>(null!)
    const [forward, setForward] = useState(true);
    useFrame((state, delta) => {
        if (forward) {
            ref.current.rotation.x += delta
            if (ref.current.rotation.x >= 0.5 * Math.PI) {
                setForward(false)
            }
        } else {
            ref.current.rotation.x -= delta
            if (ref.current.rotation.x <= -0.5 * Math.PI) {
                setForward(true)
            }
        }
    })
    return (
        <group rotation={new Euler(0, 0, 0.5 * Math.PI)}>
            <group ref={ref}>
                <Torus args={[18, 1, 16, 50]}>
                    <Material/>
                </Torus>
                <Cylinder args={[0.5, 0.5, 45, 8, 1]} rotation={new Euler(0, 0, 0.5 * Math.PI)}>
                    <Material/>
                </Cylinder>
                <Sphere args={[6, 32, 32]}>
                    <Material/>
                </Sphere>
            </group>
        </group>
    )
}

//===================== BASE ===================//
export const Base = () => {
    return (
        <group>
            <Torus args={[34, 1, 16, 50, Math.PI]} rotation={new Euler(0, 0, Math.PI)}>
                <Material/>
            </Torus>
            <Axis position={32.5} radius={1} length={5}/>
            <Sphere args={[34, 32, 32]} position={[0, -67.5, 0]}>
                <Material clippingPlanes={[new Plane(new Vector3(0, 1, 0), 51)]} side={DoubleSide}/>
            </Sphere>
        </group>
    )
}

//===================== ConcentricPendulum ===================//
export const ConcentricPendulum = () => {
    return (
        <Canvas gl={{localClippingEnabled: true}}>
            <color attach="background" args={["#FFF"]}/>
            <ambientLight intensity={100} color="#FFF"/>
            <directionalLight position={[-40, 40, 40]} intensity={2}/>

            <Base/>
            <Circle1/>

            {/*<gridHelper args={[80, 80]}/>*/}
            <PerspectiveCamera makeDefault position={[1.3 * 60, 1.3 * (40 - 10), 1.3 * 60]} zoom={1}/>
            <OrbitControls target={[0, -10, 0]}
                           enableRotate={true}
                           enableZoom={true}
                           autoRotate={true}
                           autoRotateSpeed={10}
            />
        </Canvas>
    )
}