import React from "react";
import style from "./Header.module.scss"
import {svgIcons} from "../../assets/svgIcons";
import IconButton from "@mui/material/IconButton/IconButton";
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import {observer} from "mobx-react-lite";
import {useStore} from "../../store/useStore";
import {Link, useLocation} from "react-router-dom";
import {routes} from "../A0_App/routes";


export const Header = observer(() => {
    const {appStore: {burgerMenu, setBurgerMenu}} = useStore();

    const location = useLocation();

    const elem = routes.find(el => el.path === location.pathname)
    const label = elem ? elem.label : ""

    return (
        <header className={style.header}>
            <div className={style.left}>
                <Link className={style.logoWrapper}
                      to="/"
                >
                    <div className={style.threejs}>
                        {svgIcons.threejs}
                    </div>

                    <div className={style.fiber}/>

                </Link>
                {label && <p className={style.label}>{label}</p>}
            </div>


            <IconButton className={style.burgerMenu}
                        onClick={() => setBurgerMenu(!burgerMenu)}
                        size="small"
            >
                {
                    burgerMenu
                        ? <CloseIcon fontSize='large' sx={{color: "#FFF"}}/>
                        : <MenuIcon fontSize='large' sx={{color: "#FFF"}}/>
                }
            </IconButton>

        </header>
    )
})