import {Canvas, useFrame} from "@react-three/fiber";
import React, {FC, useRef} from "react";
import * as THREE from "three";
import {PerspectiveCamera} from "@react-three/drei";
import {random, randomInteger} from "../../helpers/helpers";

const COUNT = 1000;
const MIN_DISTANCE_X = -10;
const MAX_DISTANCE_X = 60;
const MIN_DISTANCE_Y = -10;
const MAX_DISTANCE_Y = 60;
const MIN_DISTANCE_Z = -150;
const MAX_DISTANCE_Z = 80;
const SPEED = 10;

export const Box: FC<JSX.IntrinsicElements['mesh']> = (props) => {
    const ref = useRef<THREE.Mesh>(null!)

    useFrame((state, delta) => {
        state.camera.lookAt(0, 0, 0);

        if (ref.current.position.z > MAX_DISTANCE_Z) {
            ref.current.position.z = MIN_DISTANCE_Z;
        } else {
            ref.current.position.z += SPEED * delta;
        }
    })

    return (
        <mesh {...props}
              ref={ref}
        >
            <boxGeometry args={[1, 1, 1]}/>
            <meshBasicMaterial
                color={`rgb(${randomInteger(0, 255)},${randomInteger(0, 255)},${randomInteger(0, 255)})`}
                wireframe={true}
            />
        </mesh>
    )
}

export const MovingArray = () => {

    const array = [];
    for (let i = 0; i < COUNT; i++) {
        array.push(i);
    }

    // @ts-ignore
    return (
        <Canvas>
            {/*@ts-ignore*/}
            <color attach="background" args={["black"]}/>

            {/*<ambientLight intensity={0.5}/>*/}
            {/*<spotLight position={[10, 10, 10]} angle={0.15} penumbra={1}/>*/}

            {
                array.map((el) => (
                    <Box key={el}
                         position={[
                             random(MIN_DISTANCE_X, MAX_DISTANCE_X),
                             random(MIN_DISTANCE_Y, MAX_DISTANCE_Y),
                             random(MIN_DISTANCE_Z, MAX_DISTANCE_Z)
                         ]}
                    />
                ))
            }

            <PerspectiveCamera makeDefault
                               position={[45, 30, 15]}
            />
            {/*<OrbitControls target={[0,0,0]} enableRotate={false} enableZoom={false}/>*/}

        </Canvas>
    )
}