import {Canvas} from "@react-three/fiber";
import React, { Suspense } from "react";
import {Box, OrbitControls, PerspectiveCamera} from "@react-three/drei";
import {GLTFModel} from "./GLTFModel";
import style from "./GLTF.module.scss"

export const GLTF = () => {
    return (
        <div className={style.gltf}>
            <div className={style.panel}>

            </div>
            <Canvas>
                <color attach="background" args={["green"]}/>
                <ambientLight intensity={0.5}/>
                <directionalLight position={[-1, 2, 4]} intensity={2}/>

                <group>
                    <Suspense fallback={null}>
                        <GLTFModel/>
                    </Suspense>
                </group>

                <PerspectiveCamera makeDefault position={[6, 6, 6]} zoom={1} />
                <OrbitControls target={[0, 0, 0]} enableRotate={true} enableZoom={true}/>
            </Canvas>
        </div>

    )
}
