import {Canvas, useFrame, useLoader} from "@react-three/fiber";
import { ThreeMFLoader } from "three/examples/jsm/loaders/3MFLoader";
import {OrbitControls, PerspectiveCamera} from "@react-three/drei";
import React, {Suspense, useRef} from "react";
import {Euler, Group} from "three";

const LoadingModel = () => {
    const model = useLoader(ThreeMFLoader, process.env.PUBLIC_URL + "/drill/drill.3MF");
    return (
        <group rotation={new Euler(0, Math.PI, 0)}>
            <primitive object={model}
                       scale={0.005}
                       position={[0,0,-0.5]}
            >
                <meshPhongMaterial
                    color="yellow"
                    emissive="yellow"
                />
            </primitive>
            <axesHelper args={[0.1]}/>
            <gridHelper args={[3, 20]}/>
        </group>
        )
};

export const ThreeMFLoaderComponent = () => {
    return (
        <Canvas>
            <color attach="background" args={["#000"]}/>
            <ambientLight intensity={0.5}/>
            <directionalLight position={[-1, 2, 4]} intensity={2}/>

            <Suspense fallback={null}>
                <LoadingModel/>
            </Suspense>

            <PerspectiveCamera makeDefault position={[3, 2.4, 3]} zoom={2.5} />
            <OrbitControls target={[0, 0, 0]}
                           enableRotate={true}
                           enableZoom={true}
                           autoRotate={true}
                           autoRotateSpeed={10}
            />
        </Canvas>
    )
}