export const random = (min: number, max: number): number => {
    return min + Math.random() * (max - min);
}

export const randomInteger = (min: number, max: number) => {
    let rand = min - 0.5 + Math.random() * (max - min + 1);
    return Math.round(rand);
}

export const getIndexStart = (array: number[], index: number):number => {
    let indexStart = 0;
    if (index === 0) return indexStart
    for (let i = 0; i < index; i++) {
        indexStart = indexStart + 3 * array[i]
    }
    return indexStart
}

export const getIndexEnd = (array: number[], index: number): number => {
    let indexEnd = 0;
    for (let i = 0; i < index + 1; i++) {
        indexEnd = indexEnd + 3 * array[i]
    }
    return indexEnd
}
