import React, {FC, useEffect, useRef} from "react";
import {useGLTF, useAnimations} from "@react-three/drei";
import {GLTF} from 'three/examples/jsm/loaders/GLTFLoader'

type GLTFResultType = GLTF & {
    nodes: {
        Cube: THREE.Mesh
    }
    materials: {
        Material: THREE.MeshStandardMaterial
    }
}

export const GLTFModel: FC<JSX.IntrinsicElements['group']> = (props) => {

    const groupRef = useRef<THREE.Group>(null!)
    const gltf = useGLTF(process.env.PUBLIC_URL + "/animation1/animation1.gltf") as unknown as GLTFResultType;
    const {nodes, materials, animations} = gltf
    const animationsResult = useAnimations(animations, groupRef);
    const {actions, names} = animationsResult;
    console.log(gltf)
    console.log(animationsResult)

    useEffect(() => {
        actions[names[0]]?.play()
        actions[names[1]]?.play()
    }, [])


    return (
        <group ref={groupRef} dispose={null} {...props}>
            <group name="Scene">
                <group name="Light" position={[4.08, 5.9, -1.01]} rotation={[1.89, 0.88, -2.05]}/>
                {/*<group name="Camera" position={[7.36, 4.96, 6.93]} rotation={[1.24, 0.33, -0.76]}/>*/}
                <mesh name="Cube"
                      geometry={nodes.Cube.geometry}
                      //material={materials.Material}
                      position={[0, 0, 6.48]}
                >
                    <meshStandardMaterial color="red"/>
                </mesh>
            </group>
        </group>
    );
}
useGLTF.preload(process.env.PUBLIC_URL + "/animation1/animation1.gltf");