import {Canvas, createPortal, useFrame} from "@react-three/fiber";
import React, {useMemo, useRef} from "react";
import {Box, OrbitControls, PerspectiveCamera} from "@react-three/drei";
import * as THREE from "three";

const OMEGA_TARGET = 1;
const OMEGA_RENDERED = 1;

const RenderedBox = () => {
    const ref = useRef<THREE.Mesh>(null!)
    useFrame((state, delta) => {
            ref.current.rotateOnAxis(new THREE.Vector3(0, 1, 0), -OMEGA_RENDERED * delta)
        }
    )

    return (
        <Box args={[0.75, 0.75, 0.75]} ref={ref}>
            <meshPhongMaterial color="green"/>
        </Box>
    )
}

const BoxTarget = () => {
    const cameraRef = useRef<THREE.PerspectiveCamera>(null!);
    const [scene, target] = useMemo(() => {
        const scene = new THREE.Scene();
        scene.background = new THREE.Color("blue");
        const light = new THREE.DirectionalLight("#FFF", 1);
        light.position.set(-1, 2, 4);
        scene.add(light);
        const target = new THREE.WebGLRenderTarget(1024, 1024);
        return [scene, target]
    }, [])

    const ref = useRef<THREE.Points>(null!)
    useFrame((state, delta) => {
        cameraRef.current.aspect = 1;
        cameraRef.current.updateProjectionMatrix();
        cameraRef.current.lookAt(0,-0.125,0);
        ref.current.rotateOnAxis(new THREE.Vector3(0, 1, 0), OMEGA_TARGET * delta);
        state.gl.setRenderTarget(target);
        state.gl.render(scene, cameraRef.current)
        state.gl.setRenderTarget(null)
    })

    return (
        <>
            <PerspectiveCamera ref={cameraRef} position={[1.25, 0.5, 1.25]}/>
            {createPortal(<RenderedBox/>, scene)}
            <Box args={[1, 1, 1]} ref={ref}>
                <meshStandardMaterial map={target.texture}/>
            </Box>
        </>

    )
}

//===================== SCENE =====================//
export const TargetRender = () => {
    return (
        <Canvas>
            <color attach="background" args={["lightgrey"]}/>
            <ambientLight intensity={1}/>
            <directionalLight position={[-1, 2, 4]} intensity={2}/>
            <PerspectiveCamera makeDefault position={[1.5, 0.75, 1.5]}/>
            <OrbitControls target={[0, 0, 0]} enableRotate={true} enableZoom={true}/>
            <BoxTarget/>
        </Canvas>
    )
}
